import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, NgForm } from '@angular/forms';
import { ProductService } from '../../services/product.service';
import { CategoryService } from '../../services/category.service';
import { Router } from '@angular/router';

import { UPLOAD_DIRECTIVES } from 'ng2-file-uploader/ng2-file-uploader';

@Component({
  selector: 'app-create',
  templateUrl: './create.product.component.html',
  styles: []
})
export class CreateProductComponent implements OnInit {

  uploadedFiles: any[] = [];
  categories: any[] = [];
  options: Object = {
    url: 'http://localhost:1300/api/file'
  };
  productForm: FormGroup;

  productService: ProductService;
  categoryService: CategoryService;

  constructor(formbuilder: FormBuilder, private router: Router, _productService: ProductService, _categoryService: CategoryService) {

    this.productService = _productService;
    this.categoryService = _categoryService;

    this.productForm = formbuilder.group({
      'name': [null, [Validators.required]],
      'unitPrice': [null, [Validators.required]],
      'imagePath': [null, [Validators.required]],
      'categoryId': ['', [Validators.required]]
    });
  }

  ngOnInit() {
    this.categoryService
      .getAll()
      .subscribe((data: any) => {
        this.categories = data;
      });
  }

  handleUpload(data): void {
    if (data && data.response) {
      data = JSON.parse(data.response);
      this.productForm.controls['imagePath'].setValue(data.filePath);
      console.log(this.productForm.controls['imagePath'].value);
    }
  }
  saveData(form: NgForm) {
    // console.log(form.value);
    if (form.valid) {
      this.productService
        .add(form.value)
        .subscribe((data: any) => {
          this.router.navigate(['/admin/product']);
        });
    }
  }

}



