import { NgModule, ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AdminDashboardComponent } from './admin/admin.dashboard.component';
import { UserDashboardComponent } from './user/user.dashboard.component';
import { AppComponent } from './app.component';
import { StoreComponent } from './public/store.component';
import { CartComponent } from './public/cart.component';
import { LoginComponent } from './public/login.component';
import { SignupComponent } from './public/signup.component';
import { AdminLayoutComponent } from './shared/admin-layout.component';
import { UserLayoutComponent } from './shared/user-layout.component';
import { PublicLayoutComponent } from './shared/public-layout.component';
import { PagenotfoundComponent } from './shared/pagenotfound.component';
import { ProductsComponent } from './admin/product/products.component';
import { CreateProductComponent } from './admin/product/create.product.component';
import { AdminAuthGuard, UserAuthGuard } from './services/authGuard.service';
import { UnauthorizeComponent } from './public/unauthorize.component';

const adminRoutes: Routes = [
    { path: '', component: AdminDashboardComponent, pathMatch: 'full' },
    { path: 'product', component: ProductsComponent },
    { path: 'product/create', component: CreateProductComponent },
    { path: 'product/edit/:id', component: CreateProductComponent }
];
const userRoutes: Routes = [
    { path: '', component: UserDashboardComponent, pathMatch: 'full' },
];
const publicRoutes: Routes = [
    { path: '', component: StoreComponent, pathMatch: 'full' },
    { path: 'cart', component: CartComponent },
    { path: 'login', component: LoginComponent },
    { path: 'signup', component: SignupComponent }
];

const routes: Routes = [
    { path: '', component: PublicLayoutComponent, children: publicRoutes },
    { path: 'admin', component: AdminLayoutComponent, children: adminRoutes, canActivate:[AdminAuthGuard] },
    { path: 'user', component: UserLayoutComponent, children: userRoutes, canActivate:[UserAuthGuard] },
    { path: 'unauthorize', component: UnauthorizeComponent },
    { path: '**', component: PagenotfoundComponent }
];

export const routing: ModuleWithProviders = RouterModule.forRoot(routes);


