import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { UPLOAD_DIRECTIVES } from 'ng2-file-uploader/ng2-file-uploader';

import { AppComponent } from './app.component';
import { StoreComponent } from './public/store.component';
import { LoginComponent } from './public/login.component';
import { SignupComponent } from './public/signup.component';
import { CartComponent } from './public/cart.component';
import { PublicLayoutComponent } from './shared/public-layout.component';
import { AdminLayoutComponent } from './shared/admin-layout.component';
import { UserLayoutComponent } from './shared/user-layout.component';
import { PagenotfoundComponent } from './shared/pagenotfound.component';
import { routing } from './routing';
import { UserDashboardComponent } from './user/user.dashboard.component';
import { AdminDashboardComponent } from './admin/admin.dashboard.component';
import { AuthService } from './services/auth.service';
import { CategoryService } from './services/category.service';
import { ProductService } from './services/product.service';
import { StoreService } from './services/store.service';
import { AdminAuthGuard, UserAuthGuard } from './services/authGuard.service';
import { GlobalConfig } from './config/global.config';
import { ProductsComponent } from './admin/product/products.component';
import { CreateProductComponent } from './admin/product/create.product.component';
import { UnauthorizeComponent } from './public/unauthorize.component';
import { Cart } from './models/cart';

@NgModule({
  declarations: [
    AppComponent,
    StoreComponent,
    LoginComponent,
    SignupComponent,
    CartComponent,
    UserDashboardComponent,
    AdminDashboardComponent,
    PublicLayoutComponent,
    AdminLayoutComponent,
    UserLayoutComponent,
    PagenotfoundComponent,
    ProductsComponent,
    CreateProductComponent,
    UPLOAD_DIRECTIVES,
    UnauthorizeComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    routing
  ],
  providers: [AuthService, CategoryService, Cart, ProductService, StoreService, AdminAuthGuard, UserAuthGuard, GlobalConfig],
  bootstrap: [AppComponent]
})
export class AppModule { }



